<template lang="pug">
    .groups__content
        h4.groups__title Группы
        .groups__filter
            .groups__filter-item
                label.label__filter Выберите департамент:
                select(@change="onChangeDepartment()" v-model="selectedDepartmentId").groups__filter-select
                    option(value=0) Все
                    option(:value='item.id' v-for='item in departments') {{item.name}}
            .groups__filter-item
                label.label__filter Выберите преподавателя:
                select(v-model="selectedTeacherId").groups__filter-select
                    option(value=0) Все
                    option(:value='item.id' v-for='item in filteredTeachers') {{item.lastName}} {{item.firstName}}
            .groups__filter-item
                label.label__filter Выберите предмет:
                select(v-model="selectedSubjectId").groups__filter-select
                    option(value=0) Все
                    option(:value='item.id' v-for='item in filteredSubjects') {{item.name}}
            .groups__filter-item
                label.label__filter(for='classes') Выберите класс:
                select(name='classes' v-model="selectedClass").groups__filter-select
                    option(:value='0') Все
                    option(:value='item' v-for='item in 12') {{item}}
            .groups__filter-item
                label.label__filter Выберите язык:
                select(v-model="selectedLanguage").groups__filter-select
                    option(value='') Все
                    option(value='Казахский') Казахский
                    option(value='Русский') Русский
            .groups__filter-item
                label.label__filter Выберите группу:
                select(v-model="selectedGroupId").groups__filter-select
                    option(value=0) Все
                    option(:value='item.id' v-for='item in filteredGroups') {{item.code}}
        .groups__container-admin
            ul.groups__list
                li.groups__item-names
                    .groups__number #
                    .groups__code Код группы
                    .groups__subject  Предмет
                    .groups__class  Класс
                    .groups__scholars Количество обучающихся
                swiper.swiper(:options="swiperOption")
                    swiper-slide(v-for='item in slidesNumber')
                        li.groups__item(
                            v-for="group,idx in filteredGroups" 
                            v-if='(item-1)*steps <= idx && idx< item*steps'
                            :key="group.id"
                            )
                            group-item(
                                :index='idx'
                                :group='group'
                                :departments='departments'
                                :groupItemContainerVisible='groupItemContainerVisible'
                            )
                    .swiper-pagination.swiper-pagination-bullets(slot="pagination")
        button(@click.prevent='addGroup' id='addGroup').add__group Добавить группу
        add-group-form(
            v-if='addGroupFormVisible'
            v-on:eventNewGroup='eventNewGroup'
            :departments='departments'
            :teachers='teachers'
            :subjects='subjects'
        )
        
</template>
<script>
import { mapState, mapActions } from "vuex";
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
export default {
    components: {
        groupItem: () => import("../../admin/groups/group-item"),
        addGroupForm: () => import("../../admin/groups/add-group-form"),
        Swiper,
        SwiperSlide
    },
    created() {
        this.fetchDepartments(),
        this.fetchGroups(),
        this.fetchSubjects(),
        this.fetchTeachers()
    },
    data () {
        return {
            steps: 4,
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    renderBullet(index, className) {
                    return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
                    }
                }
            },
            groupItemContainerVisible: false,
            addGroupFormVisible: false,
            selectedLanguage: '',
            selectedSubjectId: 0,
            selectedClass: 0,
            selectedTeacherId: 0,
            selectedDepartmentId: 0,
            selectedGroupId: 0   
        }
    },
    computed: {
        ...mapState ("departments", {
            departments: state => state.departments}),
        ...mapState ("groups", {
            groups: state => state.groups}),
        ...mapState ("subjects", {
            subjects: state => state.subjects}),
        ...mapState ("teachers", {
            teachers: state => state.teachers}),
        filteredTeachers: function() {
            return this.teachers
            //filter by department
            .filter(ele=> ele.department.id==this.selectedDepartmentId || this.selectedDepartmentId == 0)
        },
        filteredSubjects: function() {
            return this.subjects
            //filter by department
            .filter(ele=> ele.department.id==this.selectedDepartmentId || this.selectedDepartmentId == 0)
        },
        filteredGroups: function() {
            return this.groups
            //filter by department
            .filter(ele=> ele.department.id==this.selectedDepartmentId || this.selectedDepartmentId == 0)
            //filter by teacher
            .filter(ele=> ele.teacher.id==this.selectedTeacherId || this.selectedTeacherId == 0)
            //filter by subject
            .filter(ele=> ele.subject.id==this.selectedSubjectId|| this.selectedSubjectId == 0)
            //filter by class
            .filter(ele=> ele.groupClass==this.selectedClass || this.selectedClass == 0)
            //filter by language
            .filter(ele=> ele.language==this.selectedLanguage || this.selectedLanguage == '')
            //filter by group
            .filter(ele=> ele.id==this.selectedGroupId || this.selectedGroupId == 0)
        },
        slidesNumber: function(){
            return (Math.floor(this.filteredGroups.length/this.steps)==this.filteredGroups.length/this.steps)?Math.floor(this.filteredGroups.length/this.steps):Math.floor(this.filteredGroups.length/this.steps)+1
        }
    },
    methods: {
        ...mapActions("departments", ["fetchDepartments"]),
        ...mapActions("groups", ["fetchGroups"]),
        ...mapActions("subjects", ["fetchSubjects"]),
        ...mapActions("teachers", ["fetchTeachers"]),
        addGroup(){
            this.addGroupFormVisible=!this.addGroupFormVisible
            document.getElementById('addGroup').setAttribute("disabled", "true")
        },
        eventNewGroup() {
            this.addGroupFormVisible=!this.addGroupFormVisible
            document.getElementById('addGroup').removeAttribute("disabled")
        },
        onChangeDepartment() {
            this.selectedSubjectId = 0
            this.selectedTeacherId = 0
            this.selectedLanguage = ''
            this.selectedGroupId = 0
        }
    }
  }
</script>
